<template>
  <div class="card">
    <div class="card-body">
      <div class="row">
        <div class="col-xl-12 col-lg-12">
          <div>
            <a-tabs defaultActiveKey="1">
              <a-tab-pane tab="Customer Data" key="1">
                <a-table
                  :columns="bestsellersColumns"
                  :dataSource="usersData"
                  class="utils__scrollTable"
                  :scroll="{ x: '100%' }"
                >
                  <a slot="customer_name" @click="setModal1Visible(text.id,true)" slot-scope="text" href="javascript: void(0);"> {{text.name}}</a>
                  <span slot="business_name" slot-scope="text">{{text}}</span>
                  <button @click="deleteClients(text.url)" class="btn btn-info" slot="action" slot-scope="text" >{{text.name}}</button>
                </a-table>
              </a-tab-pane>
            </a-tabs>
          </div>
        </div>
      </div>

      <a-modal
        title="Customer Information"
        style="top: 20px;"
        width="1200px"
        v-model="modal1Visible"
        @ok="() => modal1Visible = false"
      >
        <template slot="footer">
          <a-button key="submit" type="primary" @click="() => modal1Visible = false">
            Ok
          </a-button>
        </template>

        <cui-invoice :data="modalData" />

      </a-modal>

    </div>
  </div>
</template>

<script>
import {
  bestsellersData,
} from './data.json'
import CuiInvoice from '@/components/CleanUIComponents/Invoice/customer_single'

const bestsellersColumns = [
  {
    title: 'ID',
    dataIndex: 'id',
    key: 'id',
    sorter: (a, b) => a.id - b.id,
  },
  {
    title: 'Customer Name',
    dataIndex: 'customer_name',
    key: 'customer_name',
    scopedSlots: { customRender: 'customer_name' },
  },
  {
    title: 'Business Name',
    dataIndex: 'business_name',
    key: 'business_name',
    scopedSlots: { customRender: 'business_name' },
    sorter: (a, b) => a.business_name - b.business_name,
  },
  {
    title: 'Phone Number',
    dataIndex: 'phone',
    key: 'phone',
  },
  {
    title: 'Email Address',
    dataIndex: 'email',
    key: 'email',
  },
  {
    title: 'Action',
    dataIndex: 'action',
    key: 'action',
    scopedSlots: { customRender: 'action' },
  },
]

export default {
  data: function () {
    return {
      bestsellersData,
      bestsellersColumns,
      usersData: [],
      modal1Visible: false,
      modalData: {},
    }
  },
  components: {
    CuiInvoice,
  },
  mounted() {
    this.getAllCustomers()
  },
  methods: {
    setModal1Visible: function (value, ops) {
      // console.log(value)
      this.modal1Visible = ops
      var data = this.usersData
      var query = value
      // search through the array and get the index array with the passed value
      var index = data.map(function (img) { return img.id }).indexOf(query)

      const dt = {
        address: data[index].address,
        business_name: data[index].business_name,
        customer_name: data[index].customer_name.name,
        email: data[index].email,
        id: data[index].id,
        phone: data[index].phone,
        action: {
          print: {
            name: 'Print',
          },
          download: {
            action: '',
          },
          delete: {
            url: '',
          },
        },
      }
      // console.log(dt)
      this.modalData = dt
    },

    deleteClients(id) {
      this.$nprogress.start()
      const url = 'clients/delete/' + id
      this.axios.get(url)
        .then(response => {
          this.$nprogress.done()
          // console.log(response)
          if (response.status === 200) {
            this.$notification['success']({
              message: 'Customer Data',
              description: 'You have successfully Deleted a Customer ',
            })

            const data = this.usersData
            const query = id
            // search through the array and get the index array with the passed value
            const index = data.map(function (img) {
              return img.id
            }).indexOf(query)
            // filter through the data, remove the value we don't want (with index) and return the values we need
            const filteredItems = data.slice(0, index).concat(data.slice(index + 1, data.length))
            // replace the data with the returned value
            this.usersData = filteredItems
          }
        })
        .catch(error => {
          // console.log(error)
          this.$notification['success']({
            message: 'Customer Data',
            description: 'Sorry an error occurred',
          })
          return error
        })
    },
    getAllCustomers() {
      this.axios.get('clients')
        .then(response => {
          // console.log('clients', response.data.data.client)
          const dt = response.data.data.client
          const data = []
          for (let i = 0; i < dt.length; i++) {
            const userData = {
              customer_name: {
                name: dt[i].full_name,
                id: dt[i].id,
              },
              address: dt[i].address,
              business_name: dt[i].business_name,
              phone: dt[i].phone,
              email: dt[i].email,
              id: dt[i].id,
              key: dt[i].id,
              action: {
                name: 'Delete',
                url: dt[i].id,
              },
            }
            data.push(userData)
          }
          this.usersData = data
        })
        .catch(error => {
          console.log(error)
          return error
        })
    },
  },
}
</script>
