            <th>TOTAL USD</th>
            <th>TOTAL NGN</th>
            <th>Container No</th>
          </tr>
          <tr v-for="(user, index) in usersData" :key="index">
            <td>Alfreds Futterkiste</td>
            <td>Maria Anders</td>
            <td>Germany</td>
            <td>Germany</td>
            <td>Germany</td>
            <td>Germany</td>
            <td>Germany</td>
          </tr>
