<script>
import { invoiceData } from './data.json'
import jsPDF from 'jspdf'
import Printd from 'printd'
import htmlToImage from 'html-to-image'

const { invoiceContent } = invoiceData
const columns = [
  {
    title: 'ID',
    dataIndex: 'id',
    key: 'id',
    sorter: (a, b) => a.id - b.id,
  },
  {
    title: 'Shipment Mark',
    dataIndex: 'shipping_mark',
    key: 'shipping_mark',
    scopedSlots: { customRender: 'shipping_mark' },
  },
  {
    title: 'P/N',
    dataIndex: 'phone',
    key: 'phone',
    scopedSlots: { customRender: 'phone' },
    sorter: (a, b) => a.phone - b.phone,
  },
  {
    title: 'CTN',
    dataIndex: 'ctn',
    key: 'ctn',
  },
  {
    title: 'CBM',
    dataIndex: 'cbm',
    key: 'cbm',
  },
  {
    title: 'TOTAL USD',
    dataIndex: 'usd',
    key: 'usd',
    scopedSlots: { customRender: 'usd' },
  },
  {
    title: 'TOTAL IN NAIRA',
    dataIndex: 'naira',
    key: 'naira',
    scopedSlots: { customRender: 'naira' },
  },
  {
    title: 'Container No',
    dataIndex: 'container',
    key: 'container',
  },
  {
    title: 'Action',
    dataIndex: 'action',
    key: 'action',
    scopedSlots: { customRender: 'action' },
  },
]

export default {
  name: 'CuiInvoice',
  data: function () {
    return {
      columns,
      invoiceData,
      invoiceContent,
      invoiceName: '',
      usersData: [],
      loadingDelete: false,
      loadingPrint: false,
      loadingDownload: false,
      loading: false,
      visible: false,
      modalData: [],
    }
  },
  props: {
    data: Object,
  },
  mounted() {
    this.invoiceName = this.$route.params.invoice_number
    this.getCustomerShipment(this.$props.data.id)

    window.onafterprint = function(event) {
      console.log(event)
    }

  },
  methods: {
    printOne(id) {
      // console.log(id)
      var data = this.usersData
      var query = id
      // search through the array and get the index array with the passed value
      var index = data.map(function (img) { return img.id }).indexOf(query)
      const dt = data[index]
      const dat = []
      const userData = {
        shipping_mark: {
          name: dt.shipping_mark.name,
        },
        phone: dt.phone,
        ctn: dt.ctn,
        cbm: dt.cbm,
        usd: dt.usd,
        naira: dt.naira,
        container: dt.container,
        id: dt.id,
        key: dt.id,
      }
      dat.push(userData)
      this.modalData = dat
      this.visible = true
    },
    showModal() {
      this.visible = true
      this.modalData = this.usersData
    },
    handleOk(e) {
      this.print()
    },
    handleCancel(e) {
      this.visible = false
    },
    getCustomerShipment(id) {
      this.$nprogress.start()
      this.axios.get(`clients/shipments/${id}`)
        .then(response => {
          this.$nprogress.done()
          // console.log(response.data.data.shipments)
          const dt = response.data.data.shipments
          const data = []
          for (let i = 0; i < dt.length; i++) {
            const userData = {
              shipping_mark: {
                name: dt[i].shipping_mark,
                id: dt[i].id,
              },
              phone: dt[i].phone,
              ctn: dt[i].ctn,
              cbm: dt[i].cbn,
              usd: dt[i].total_usd,
              naira: dt[i].total_naira,
              container: dt[i].container_no,
              id: dt[i].id,
              key: dt[i].id,
              action: {
                name: 'Print',
                id: dt[i].id,
              },
            }
            data.push(userData)
            this.usersData = data
          }
        })
        .catch(error => {
          // console.log(error)
          return error
        })
    },
    print() {
      this.loadingPrint = true
      console.log('printing')
      var printContents = document.getElementById('printDocument').innerHTML
      const d = new Printd()
      const cssText = `
        table {
          font-family: arial, sans-serif;
          border-collapse: collapse;
          width: 100%;
          margin-top: 20px;
        }

        td, th {
          border: 1px solid #dddddd;
          text-align: left;
          padding: 8px;
        }

        th {
          padding: 20px;
          background-color: #fafafa;
          border-top-left-radius: 3px;
          border-top-right-radius: 3px;
        }

        tr:nth-child(even) {
          background-color: #dddddd;
        }
      `
      const printCallback = ({ launchPrint }) => launchPrint()

      d.print(document.getElementById('printDocument'), [cssText], '', printCallback)

    // document.getElementById('printDocument').classList.add('sheet')
      // document.getElementById('printDocument').classList.add('padding-10mm')
      // // console.log(printContents)
      // var originalContents = document.body.innerHTML
      // document.body.innerHTML = printContents
      // window.print()
      // document.body.innerHTML = originalContents
    },
    showDeleteConfirm() {
      this.$confirm({
        title: 'Are you sure delete this customer?',
        content: 'Doing this can\'t be undone',
        okText: 'Yes',
        okType: 'danger',
        cancelText: 'No',
        onOk() {
          console.log('OK')
        },
        onCancel() {
          console.log('Cancel')
        },
      })
    },
    downloadPdf() {
      this.$nprogress.start()
      const name = this.$props.data.customer_name.replace(/\s/g, '_') + '_data'
      htmlToImage.toJpeg(document.getElementById('dwnlo'), { quality: 0.95 })
        .then(function (dataUrl) {
          // this.$nprogress.done()
          // console.log(dataUrl)
          // $(".hidden-img").html(`<img src='${dataUrl}'>`);
          // download(dataUrl, certName + '.jpg');
          var imgData = dataUrl
          var doc = new jsPDF()
          doc.addImage(imgData, 'JPEG', 15, 15, 80, 80)
          const certName = name
          doc.save(certName + '.pdf')
        })
    },
  },
}
</script>
